*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    overflow: hidden;
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.menue{
    position:absolute;
    z-index: 2;
    left:2%;
    margin-top:2%;
    border:white 4px solid;
    border-radius:3px;
    text-align: center;
    animation:controllerRoll .3s both

}

.button{

    color:white;
    font-family: Arial, Helvetica, sans-serif;
    margin:10px;
    padding:5px;
    border-radius:3px

}
.play, .stop{

    color:white;
    font-family: Arial, Helvetica, sans-serif;
    margin:10px;
    padding:5px;
    border-radius:3px

}
.button:hover{
    background-color: white;
    color:black
}
.play:hover{
    background-color: white;
    color:black
}
.stop:hover{
    background-color: white;
    color:black
}

.invisibleP{
    display:none;
}

.monitor{
    border:4px solid white;
    border-radius:3px;
    color:white;
    font-weight:bold;
    position:absolute;
    
    width:80%;
    animation:monitorfall .3s both;
    z-index: 3;
    padding:1%;
    
    margin:10%;
    margin-top:2%;
     /* -webkit-text-stroke-width: .5px;
  -webkit-text-stroke-color: black; */
    text-shadow: 1px 1px #000000;

    
}
.monitor .news:hover, .monitor .about:hover, .monitor .contact:hover{
    background-color: #ffffff;
    color: black;
    text-shadow: 1px 1px #ffffff;

}
.monitor .news, .monitor .about, .monitor .contact{
padding:3px;

}


.xButton{

    border-radius:3px;
    padding:10px;
    margin:10px;
    position:Absolute;
    right:1%;
    top:1%;

}

.display{
    margin:10px;
    padding:10px;
    width:60%;
}

.xButton:hover{
    background-color:white;
    color:black

}

@keyframes monitorfall {
0%{transform:translateY(-100%)} 
100%{transform:translateY(0%)}   
}

@keyframes controllerRoll {
    0%{transform:translateX(-100%)} 
    100%{transform:translateX(0%)}   
    }

body{
    align-items: center;
}

a{
    color:white;
    text-decoration:none;

}

.loading{
    background-color:White;
    color:black;
    font-weight: bolder;
    border-radius:5px;
    padding:20px;
    position:absolute;
    top:50%;
    left:48%;

}
.ready{
    color:rgb(254, 254, 254);
    font-weight:bolder;
    border: 2px white solid;
    border-radius:5px;
    padding:20px;
    position:absolute;
    top:50%;
    left:48%;

}
.ready:hover{
    background-color:White;
    color:black;
    font-weight: bolder;
    border-radius:5px;
    padding:20px;
    position:absolute;
    top:50%;
    left:48%;

}


.portfolio li:hover a{

    color:black;
    background-color: white;
    text-shadow: 1px 1px #ffffff;

    

}